@import "components/variables";

section {
  padding-top: 3rem;
  padding-bottom: 3rem;
  @media (min-width: 992px) {
    padding-top: 5rem;
    padding-bottom: 5rem;
  }
}

#welcome {
  position: relative;
  padding-top: calc(75px + 5rem);
  padding-bottom: 5rem;
  @media (min-width: 992px) {
    padding-top: calc(100px + 5rem);
    // height: 100vh;
  }
  .video-welcome {
    position: absolute;
    overflow: hidden;
    background-image: url(../images/index/bg-welcome.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
    iframe#player-welcome {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      display: none;
      @media (min-width: 992px) {
        display: block;
      }
      @media (min-aspect-ratio: 16/9) {
        height: 200%;
        top: -50%;
      }
      @media (max-aspect-ratio: 16/9) {
        width: 200%;
        left: -50%;
      }
    }
    .placa-video {
      position: absolute;
      overflow: hidden;
      background: rgba($color: #000000, $alpha: 0.8);
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
  }
  .title {
    font-family: "MyriadRoman";
    font-weight: bold;
    color: $white;
    text-align: center;
    @media (min-width: 992px) {
      font-size: 3rem;
    }
    span {
      font-style: italic;
      color: $red;
    }
  }
  .boxes {
    padding-top: 5vh;
    @media (min-width: 992px) {
      padding-top: 15vh;
    }
    .box1 {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      border-radius: 2px;
      color: $white;
      background-color: $red;
      h2 {
        font-family: "MyriadRoman";
        font-weight: bold;
      }
    }
    .box2 {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      border-radius: 2px;
      color: $black;
      background-color: $white;
      h2 {
        font-family: "MyriadRoman";
        font-weight: bold;
      }
    }
  }
}

#que-buscas {
  background-color: $white;
  img {
    width: 50%;
    @media (min-width: 992px) {
      width: 75%;
    }
  }
  .text {
    text-align: center;
    display: flex;
    align-items: center;
    @media (min-width: 992px) {
      padding-left: 5rem;
      padding-right: 5rem;
    }
    h2 {
      font-family: "MyriadRoman";
      font-weight: bold;
      margin-bottom: 1.5rem;
      @media (min-width: 992px) {
        font-size: 3rem;
      }
    }
    p {
      @media (min-width: 992px) {
        font-size: 2rem;
      }
      span {
        font-weight: bold;
      }
    }
  }
}

#caracteristicas {
  background-image: url(../images/index/bg-caracteristicas.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  img {
    width: 80px;
    margin-bottom: 1rem;
    transition: transform 0.3s;
    &:hover {
      transform: scale(1.2);
    }
  }
  h2 {
    font-family: "MyriadRoman";
    font-weight: bold;
    font-style: italic;
    color: $red;
    margin-bottom: 1rem;
  }
  p {
    color: $white;
  }
}

.empresario {
  h2 {
    font-family: "MyriadRoman";
    text-align: center;
    font-weight: bold;
    margin-bottom: 2rem;
    @media (min-width: 992px) {
      font-size: 3rem;
      margin-bottom: 5rem;
    }
  }
  img {
    width: 100%;
  }
  .text {
    p {
      @media (min-width: 992px) {
        font-size: 1.5rem;
      }
    }
  }
}
#empresario1 {
  background-color: $white;
}
#empresario2 {
  background-color: $bg-black;
  h2 {
    color: $red;
  }
  p {
    color: $white;
  }
}

#blog {
  background-color: $gray1;
  h2 {
    font-family: "MyriadRoman";
    font-weight: bold;
    margin-bottom: 2rem;
    @media (min-width: 992px) {
      font-size: 3rem;
      margin-bottom: 5rem;
    }
  }
  a {
    text-decoration: none;
    color: $black;
    .card {
      border: 1px solid $gray1;
      padding: 0.5rem;
      transition: transform 0.3s;
      &:hover {
        transform: scale(1.1);
        z-index: 1;
      }
      .card-body {
        padding: 1rem 0.5rem;
        .card-title {
          font-family: "MyriadRoman";
          font-weight: bold;
        }
        .card-text {
          font-family: "MyriadRoman";
          font-size: 0.8rem;
          color: $gray2;
        }
        hr {
          background-color: $gray1;
          height: 1px;
          margin: 1.5rem 0;
          opacity: 1;
        }
        .card-firm {
          display: flex;
          align-items: center;
          img {
            width: 25%;
            height: auto;
          }
          div {
            padding-left: 1rem;
            .person {
              font-family: "MyriadRoman";
              color: $red;
              margin-bottom: 0rem;
            }
            .position {
              font-family: "MyriadRoman";
              font-size: 0.8rem;
              color: $gray2;
              margin-bottom: 0rem;
            }
          }
        }
      }
    }
  }
}

#estadisticas {
  background-color: $white;
  padding-bottom: 0;
  .question {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 2rem;
    @media (min-width: 992px) {
      justify-content: flex-end;
      padding-bottom: 0;
      padding-right: 0;
    }
    @media (min-width: 1200px) {
      padding-right: 2vw;
    }
    @media (min-width: 1400px) {
      padding-right: 10vw;
    }
    h2 {
      font-family: "MyriadRoman";
      text-align: center;
      font-size: 3rem;
      font-weight: bold;
    }
    .q1 {
      @media (min-width: 576px) {
        margin-right: 5vw;
      }
    }
    .q2 {
      @media (min-width: 576px) {
        text-align: end;
      }
    }
  }
  .alert {
    display: flex;
    align-items: center;
    background-color: $pink1;
    .check {
      position: relative;
      width: 30px;
      height: 30px;
      background: $pink2;
      border-radius: 0.25rem;
      display: inline-table;
      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 15px;
        height: 8px;
        border: 3px solid $red;
        margin-left: 8px;
        border-right: 0;
        border-top: 0;
        margin-top: 8px;
        transform: rotate(-45deg);
      }
    }
    .text {
      font-family: "MyriadRoman";
      padding-left: 1rem;
    }
  }
  .container-fluid {
    background-color: $bg-black;
    position: relative;
    .girl {
      position: absolute;
      left: -4vw;
      bottom: 0;
      width: 38vw;
      @media (min-width: 1400px) {
        left: 0;
        width: 36vw;
      }
      @media (min-width: 1600px) {
        left: 2vw;
        width: 33vw;
      }
      @media (min-width: 1800px) {
        left: 7vw;
        width: 28vw;
      }
    }
    .container {
      .border-lr {
        @media (min-width: 576px) {
          border-left: 1px solid $gray2;
          border-right: 1px solid $gray2;
        }
      }
      h2 {
        font-family: "MyriadRoman";
        text-align: center;
        color: $red;
        font-weight: bold;
        font-size: 3rem;
      }
      p {
        font-family: "MyriadRoman";
        text-align: center;
        color: $white;
        margin: 0;
      }
    }
  }
}